<template>
  <div class="Cart-container">
    <!-- navbar -->
    <van-nav-bar
      left-text="返回"
      left-arrow
      fixed
      placeholder
      @click-left="$router.back()"
    >
      <template #title>购物车({{ cartLength }})</template>
    </van-nav-bar>
    <!-- /navbar -->
    <!-- 购物车 -->
    <van-swipe-cell v-for="(item, index) in cartList" :key="item.goods_id">
      <van-card class="goods-card">
        <template #title>
          {{ item.goods_name }}
        </template>
        <template #thumb>
          <van-image :src="item.goods_logo" />
        </template>
        <template #price>{{ item.goods_price | showPrice }}</template>
        <template #num>×{{ item.goods_count }}</template>

        <template #footer>
          <van-stepper v-model="item.goods_count" />
        </template>
      </van-card>
      <!-- 滑动删除 -->
      <template #right>
        <van-button
          square
          text="删除"
          type="danger"
          class="delete-button"
          @click="removeHandle(index)"
        />
      </template>
    </van-swipe-cell>
    <!-- /购物车 -->
    <div v-if="cartLength">
      <h2>总价格：{{ totalPrice | showPrice }}</h2>
    </div>
    <h2 v-else>购物车为空</h2>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'cart',

  data() {
    return {}
  },

  computed: {
    // 两种语法
    ...mapGetters(['cartLength', 'cartList']),
    totalPrice() {
      // let totalPrice = 0
      // for (const goods of this.cartList) {
      //   totalPrice += goods.goods_price * goods.goods_count
      // }
      // return totalPrice

      // js高阶函数
      // 这里的good是一个对象
      return this.cartList.reduce(function(prevalue, good) {
        return prevalue + good.goods_price * good.goods_count
      }, 0)
    }
  },

  created() {},

  methods: {
    removeHandle(index) {
      this.cartList.splice(index, 1)
    }
  },
  // {{peice | 过滤器}}
  filters: {
    showPrice(price) {
      // toFixed() 方法可把 Number 四舍五入为指定小数位数的数字
      return '￥' + price.toFixed(2)
    }
  }
}
</script>
<style scoped lang="less">
.goods-card {
  margin: 0;
  background-color: white;
}

.delete-button {
  height: 100%;
}
</style>
